<template>
	<view>
		<view style="height: 350rpx;" class="bg-light position-relative" hover-class="bg-hover-light">
			<image v-for="(img,index) in imgList" :key="index" :src="img" mode="aspectFill" style="width: 100%;height: 350rpx;background-color: rgba(255,255,255,0.2);"></image>
			<view class="position-absolute flex flex-column align-center justify-center left-0 bottom-0 right-0 top-0">
				<text class="iconfont iconjia" style="font-size: 50rpx;" @click="setPhoto"></text>
				<text class="font text-muted">{{imgList.length==1?'点击切换封面图':'点击添加封面图'}}</text>
			</view>
		</view>
	
		<personalworksTypeDeatil title="标题">
			<input type="text" placeholder="请填写视频标题" v-model="form.title" placeholder-class="text-light-muted" class="w-100 pr-5"/>
		</personalworksTypeDeatil>
		<personalworksTypeDeatil title="分类" :rightIconShow="true">
			<!-- picker选择器，@change事件detail中只能拿到被点击选择的索引 -->
			<picker mode="selector" :range="sortRange" @change="sortSelector">
				<input type="text" placeholder="请选择分类" v-model="form.sortDeatil" placeholder-class="text-light-muted" class="w-100 pr-5"/>
			</picker>
		</personalworksTypeDeatil>
		<personalworksTypeDeatil title="描述">
			<input type="text" placeholder="请填写视频描述" v-model="form.desc" placeholder-class="text-light-muted" class="w-100 pr-5"/>
		</personalworksTypeDeatil>
		
		
		<view class="mt-4"></view>
		<myToSeend>立即发布</myToSeend>
		
       
		
	</view>
</template>

<script>
	import personalworksTypeDeatil from '@/componets/personalworksTypeDeatil/personalworksTypeDeatil.vue';
	import myToSeend from '@/componets/myToSend/MyToSend.vue';
	export default {
		components:{
			personalworksTypeDeatil,
			myToSeend
		},
		data() {
			return {
				imgList:[],
				form:{
					title:'',
					sortDeatil:"",
					desc:'',
				},
				sortRange:['text1','text2','text3','text4']
			}
		},
		methods: {
			setPhoto(){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: result => {
						this.imgList = result.tempFilePaths
					}
				});
			},
			sortSelector(detail){
				this.form.sortDeatil=this.sortRange[detail.detail.value]
			}
		}
	}
</script>

